// app/dashboard/page.tsx

'use client';

import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';

export default function DashboardPage() {
    const { user, logout, isAuthenticated } = useAuth();
    const router = useRouter();

    // Simple redirect if not authenticated (we'll make this a proper middleware later)
    if (!isAuthenticated) {
        router.push('/login');
        return <div className="min-h-screen flex items-center justify-center">Loading...</div>;
    }

    return (
        <div className="min-h-screen bg-gray-100 p-8">
            <h1 className="text-3xl font-bold mb-6">Welcome to the Dashboard, {user?.email}!</h1>
            <p className="mb-4">Your current subscription status is:
                <span className={`font-semibold ${user?.isSubscribed ? 'text-green-600' : 'text-yellow-600'}`}>
                    {user?.isSubscribed ? ' Active Subscriber' : ' Trialing / Canceled'}
                </span>
            </p>
            <p className="mb-6">This is where the magic will happen!</p>

            <button
                onClick={logout}
                className="py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-red-600 hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500"
            >
                Log Out
            </button>
        </div>
    );
}